let galleryArray = [];

images.forEach(image => {
  let galleryImg = document.createElement('img');
  galleryImg.src = image.preview;
  galleryImg.alt = image.description;
  galleryImg.classList.add('gallery-image');

  let galleryLink = document.createElement('a');
  galleryLink.classList.add('gallery-link');
  galleryLink.href = image.original;

  galleryLink.append(galleryImg);

  let galleryItem = document.createElement('li');
  galleryItem.classList.add('gallery-item');
  galleryItem.append(galleryLink);
  galleryArray.push(galleryItem);
});

let gallery = document.querySelector('.gallery');
console.log(gallery);

gallery.append(...galleryArray);
console.log(galleryArray);

const sssss = new SimpleLightbox('.gallery-link', {
  captionDelay: 250,
  captionsData: 'alt',
});

console.log(sssss);
